import React, { Component, useState, useCallback  } from 'react';
import { connect } from 'react-redux';
import { useHistory, useParams, Link } from 'react-router-dom';

import ImageViewer from 'react-simple-image-viewer';

import { url } from '../redux/api/endpoints';
// actions
import ShopActions from '../redux/redux-sauce/shops';
import FeedbacksActions from '../redux/redux-sauce/feedbacks';

import { FeedbackCreateForm, FeedbackEditForm } from '../components/ShopForms';
import { CatButtonComp } from './Main';


function Feedback (props) {
  const { feedback } = props
  return (
    <div className='card card-style mx-0 my-1'>
      <div className='content'>
        <p className='mb-1 pl-2 font-700'>{feedback.nickname}</p>
        <p className='mb-1 pl-2'>{feedback.text}</p>
      </div>
    </div>
  )
}

function OwnFeedback (props) {
  const { feedback } = props
  const [ editOnOff, setEditOnOff] = useState(false)
    return (
    editOnOff 
    ? <FeedbackEditForm 
       initialValues={{text: feedback.text, id: feedback.id}}
       parentSubmit={props.edit}
       setEditOnOff={setEditOnOff}
       />
    : <div className='card card-style mx-0 my-1'>
        <div className='content'>
          <p className='mb-1 pl-2 font-700'>{feedback.nickname}</p>
          <p className='mb-1 pl-2'>{feedback.text}</p>
          <button className='btn btn-sx rounded-s shadow-s' onClick={() => setEditOnOff(true)}>
            изменить
          </button>
          <button className='btn btn-sx rounded-s shadow-s bg-red1-light float-right' 
            onClick={() => props.delete(feedback.id)}>
            удалить
          </button>
        </div>
      </div>
  )
}


function Feedbacks(props) {
  const { shop_id, feedback_list, user_id, whoLikeList, whoFeedbackList } = props
  const [ showFeed, setShowFeed ] = useState(false)
   
  let isLike = whoLikeList ? whoLikeList.includes(user_id) : false
  let isFeedback = whoFeedbackList ? whoFeedbackList.includes(user_id) : false

  const feedbackCount = whoFeedbackList ? whoFeedbackList.length : 0

  function showOrCreateFeedbacks () {
    props.showFeedbacks(shop_id)
    setShowFeed(!showFeed)
  }

  function likeUnlikeShop () {
    props.likeShop(shop_id)
  }

  return (
    [
      <div className='content my-1'>
        <p className='float-left mr-4 border rounded-s px-1 py-1 my-1 px-3' onClick={likeUnlikeShop}>
          Нравится
          {isLike 
            ? <span className='color-yellow1-light ml-1'>
                <i className="fas fa-heart color-red2-light"></i> {' '}
              </span>
            : <span className=''>
                <i className="far fa-heart color-red2-light ml-1"></i> {' '}
              </span>
          }
        </p>
        <p onClick={showOrCreateFeedbacks} className='float-right border rounded-s px-2 py-1 ml-1 my-1'>
          Отзывы <i className="far fa-comments ml-1"></i> {feedbackCount > 0 ? feedbackCount : 0}
        </p>
        <div className='clearfix'></div>
        {(showFeed && isFeedback) &&
          <p className='my-0'>Вы уже оставляли отзыв</p>
        }
      </div>,

      showFeed &&
        user_id
        ? !isFeedback && 
          <FeedbackCreateForm 
            initialValues={{shop: shop_id}} 
            parentSubmit={props.createFeedback}
            addToFeedbacksList={props.addToFeedbacksList}
            user_id={user_id}/>
        : showFeed && <p className='my-0 ml-3'>Войдите чтобы оставлять отзывы.</p>
      ,

      showFeed && feedback_list.length > 0 &&
        <div className='content mt-1'>
          {feedback_list.map(fb => 
            fb.profile === user_id 
              ? <OwnFeedback feedback={fb} edit={props.editFeedback} delete={props.deleteFeedback}/>
              : <Feedback feedback={fb}/>
            )}
        </div>
    ]
  )
}


function CatButton(props) {
  let history = useHistory();
  const { catName, shopId } = useParams();
  const { cat } = props

  const className = 
  'btn btn-xs mb-1 ml-1 rounded-xl font-500 shadow-s text-wrap float-left'

  const classActive = catName == cat.name 
    ? className + ' bg-red2-light' 
    : className + ' bg-blue1-light'

  function handleClick() {
    history.push("/shops/" + shopId + '/products/' + cat.name + '/')
  }

  return (
    <CatButtonComp
      className={classActive}
      cat={cat} 
      handleClick={handleClick} 
    />
  );
}

function ProductBody(props) {
  const { product } = props

  const [currentImage, setCurrentImage] = useState(0)
  const [isViewerOpen, setIsViewerOpen] = useState(false)

  let catalogImages = []
  let thumbImages = []
  product.images.map(image => {
    catalogImages.push(url + image.catalog_image)
    thumbImages.push(url + image.thumb_image)
  })

  const openImageViewer = useCallback(index => {
    setCurrentImage(index);
    setIsViewerOpen(true);
  }, [])

  const closeImageViewer = () => {
    setCurrentImage(0);
    setIsViewerOpen(false);
  }

  return (
    <div className='shop-product-text-body'>
      <p className='font-18 mb-1 mt-1'>
        {product.title}
        <span className='my-0 font-900 font-16 float-right pr-3'>{product.price}</span>
      </p>
      <p className='my-0'>{product.description}</p>
      {product.images.length > 0 &&
        <div className='mt-1'>
            {catalogImages.map((src, index) => 
              <img className='mr-2 mt-1' 
                src={ src }
                onClick={ () => openImageViewer(index) }
                key={ index }
                width='100px'
                style={{ margin: '2px' }}
                alt="asdada"
                />
            )}
        </div>
      }
      {isViewerOpen && (
        <ImageViewer
          src={ catalogImages }
          currentIndex={ currentImage }
          onClose={ closeImageViewer }
        />
      )}
    </div>
  );
}

function CatProductList(props) {
  const { catName } = useParams();
  const { products } = props

  let catProducts = []

  if (catName && products.length > 0) {
    for (var i = 0; i < products.length; i++){
      if (products[i].category === catName){
        catProducts = [...catProducts, products[i]]
      }
    }
  }

  return (
    catProducts.map(product => 
      <div className='card card-style mb-1 mx-1' style={{'transform': 'none'}}>
        <div className='content'>
          <ProductBody product={product} />
        </div>
      </div>
      )
  )}

function ViberBlock(props) {
  const { catName } = useParams();
  const { shopDetail } = props
  const viberIconSrc = url + 'images/viber_icon.png'

  let phone = props.shopDetail.phone
  let re = /8{1}/;
  let re_phone = phone.replace(re, '7');

  return (
    <div className='my-2'>
      <a href={`viber://chat?number=${re_phone}`} 
      // <a href={`viber://chat?number=79834344216`} 
        className='btn btn-xs font-12 mb-1 mr-1 rounded-s shadow-s float-left color-mainFont-light'
        >
        Написать <img src={viberIconSrc} width='20px' />
      </a>
      <a href={`viber://forward?text=${url}/shops/${shopDetail.id}/products/${catName}/`}
        className='btn btn-sx font-12 mb-1 mr-1 rounded-s shadow-s color-mainFont-light'
        >
        Поделиться продавцом <img src={viberIconSrc} width='20px' />
      </a>
    </div>
  )
}

class ShopDetail extends Component {
  constructor(props) {
    super(props);  
    this.state = {
      catProducts: [],
      active_cat: null,
    }
    this.createFeedback = this.createFeedback.bind(this);
    this.showFeedbacks = this.showFeedbacks.bind(this);
    this.editFeedback = this.editFeedback.bind(this);
    
  }

  componentDidMount() {
    const { shopId } = this.props.match.params
    // console.log(this.props.match)
    this.props.getShopDetail(shopId)
  }

  createFeedback () {
    this.props.createFeedback(this.props.state.form.feedbackCreateForm.values)
  }

  showFeedbacks (shop_id) {
    this.props.getFeedbacks({shop: shop_id})
  }

  editFeedback () {
    this.props.updateFeedback(this.props.state.form.feedbackEditForm.values)
  }

  render() {
    const { shopDetail } = this.props.state.shops
    const { feedback_list } = this.props.state.feedbacks
    const user_id  = this.props.state.auth.user ? this.props.state.auth.user.id : null
    return (
        shopDetail &&
          <div>
            <div className='card card-style my-2 mx-0 pb-2 rounded-0'>
              <div className='content mb-1'>
                <h5 className='float-left'>{shopDetail.name}</h5>
                <p className='float-right mb-0'>
                  <i className="fas fa-heart color-red2-light ml-1" aria-hidden="true"></i>
                    {shopDetail.likes_list.length}
                  <i className="far fa-comments ml-3" aria-hidden="true"></i>
                    {shopDetail.feedbacks_list.length} 
                </p>
                <div className='clearfix'></div>
                <h5 className='font-13 my-0'><i className="fab fa-viber mr-2"></i>{shopDetail.phone}</h5>
                <ViberBlock shopDetail={shopDetail}/>
                <div className='clearfix'></div>
                <div className='my-2'>
                  {shopDetail.description}
                </div>
                <div className="divider-icon bg-gray1-dark mb-1 mt-2"></div>
                <div className=''>
                  <p className='my-0 font-14 font-600'>Доставка, самовывоз или точки продаж:</p>
                  {shopDetail.delivery ? shopDetail.delivery : 'Нет информации'}
                </div>
              </div>

            <Feedbacks 
              shop_id={shopDetail.id}
              user_id={user_id}

              createFeedback={this.createFeedback} 
              showFeedbacks={this.showFeedbacks}
              editFeedback={this.editFeedback}
              deleteFeedback={this.props.deleteFeedback}
              feedback_list={feedback_list}
              whoFeedbackList={shopDetail.feedbacks_list}
              addToFeedbacksList={this.props.addToFeedbacksList}

              whoLikeList={shopDetail.likes_list}
              likeShop={this.props.likeShop}
              />
            </div>
            <div className='card card-style mb-1 mx-0'>
              {shopDetail.categories.length > 1 &&
                <div className='content my-1'>
                  <p className='my-0 font-500'>Здесь также продается:</p>
                  {shopDetail.categories.map(cat => 
                    <CatButton cat={cat}/>
                  )}
                </div>
              }
              <div className='clearfix'></div>
            </div>
            <CatProductList products={shopDetail.products} />
          </div>
    );
  }
}

const mapStateToProps = (state) => ({
  state: state,
  routing: state.routing,
})

const mapDispatchToProps = (dispatch) => ({
  getShopDetail: id => dispatch(ShopActions.getShopDetailRequest(id)),
  likeShop: shop_id => dispatch(ShopActions.likeShopRequest(shop_id)),

  createFeedback: data => dispatch(FeedbacksActions.createFeedbackRequest(data)),
  updateFeedback: data => dispatch(FeedbacksActions.updateFeedbackRequest(data)),
  deleteFeedback: data => dispatch(FeedbacksActions.deleteFeedbackRequest(data)),
  getFeedbacks: data => dispatch(FeedbacksActions.getFeedbacksRequest(data)),
  addToFeedbacksList : user_id => dispatch(ShopActions.addToFeedbacksList(user_id)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ShopDetail);
